import { rsModel } from "./rs-model";
class rsFill{
    $fill: JQuery<HTMLElement>;
    private _width: number;
    constructor(fillClass: string){
        this.$fill = $('<div class="' + fillClass + '" />');
    }
    public get width(): number {
        let str: string = this.$fill.parent().css('width');
        return parseFloat(str.substr(0,str.length-2));
    }
    public set width(val: number) {
        this._width = val;
    }
}
class rsControl{
    private _offset: number;
    led: boolean;
    vertical: boolean;
    $control: JQuery<HTMLElement>;
    constructor(handleClass: string, led: boolean, vertical: boolean){
        this.vertical = vertical;
        this.led = led;
        if(led)
            this.$control = $('<div class="' + handleClass + '" ><input type="text"></div>');
        else
            this.$control = $('<div class="' + handleClass + '" >');
    }
    public set offset(val: number){
        this._offset = val;
    }
    public get offset(): number{
        if(this.vertical)
            return this.$control[0]['offsetHeight'];
        else
            return this.$control[0]['offsetWidth'];
    }
}
const constants: {[index: string]:any} = {
    orientation: {
        horizontal:{
            dimension: 'width',
            direction: 'left',
            directionStyle: 'left',
            coordinate: 'x'
        },
        vertical: {
            dimension: 'height',
            direction: 'top',
            directionStyle: 'bottom',
            coordinate: 'y'
        }
    }
};   

class rsView{
    rootObject: JQuery<HTMLElement>;
    $range: JQuery<HTMLElement>;
    $fillclass: rsFill;
    $fill: JQuery<HTMLElement>;
    $controlclass: rsControl;
    $control: JQuery<HTMLElement>;
    $document: JQuery<HTMLDocument>;
    rangeClass: string='rSlider1410';
    fillClass: string  = 'rSlider1410__fill';
    handleClass: string = 'rSlider1410__control';
    maxHandlePos: number; 
    vertical: boolean = false;
    grabPos: number;
    DIMENSION: any;
    DIRECTION: string;
    DIRECTION_STYLE: string;
    COORDINATE: string;
    constructor(model: rsModel, rootObject: JQuery<HTMLElement>){
        this.rootObject = rootObject;
        this.$document  = $(document);
        this.$fillclass = new rsFill(this.fillClass);
        this.$fill      = this.$fillclass.$fill;
        this.$controlclass = new rsControl(this.handleClass, true, true);
        this.$control   = this.$controlclass.$control;
        this.$range = $('<div class="' + this.rangeClass + ' '+this.rangeClass+'-'+ model.orientation+'" id="' + model.identifier + '" />').insertAfter(model.element).prepend(this.$fill, this.$control).appendTo(this.rootObject);
        this.grabPos = this.$controlclass.offset;
//        console.log(this.grabPos);
        this.maxHandlePos = this.$fillclass.width - this.grabPos;
        //console.log(this.maxHandlePos);
        //if(model.orientation === 'vertical') this.vertical=true;
        this.DIMENSION          = constants.orientation[model.orientation].dimension;
        this.DIRECTION          = constants.orientation[model.orientation].direction;
        this.DIRECTION_STYLE    = constants.orientation[model.orientation].directionStyle;
        this.COORDINATE         = constants.orientation[model.orientation].coordinate;
    };
    setPositionView(pos: number){
        if(this.vertical){
            this.$fill[0].style['height'] = (pos+this.grabPos/2) + 'px';
            this.$control[0].style['top'] = (pos) + 'px';
        }else{
            this.$fill[0].style['width'] = (pos+this.grabPos/2) + 'px';
            this.$control[0].style['left'] = (pos) + 'px';
        }
    }
    getPositionView(e: any){
        let pageCoordinate:number = 0;
        let rangePos: number;
        if(this.vertical){
            rangePos = this.$range[0].getBoundingClientRect()['bottom'];
            pageCoordinate = e.originalEvent['clientY'];
        }else{
            rangePos = this.$range[0].getBoundingClientRect()['left'];
            pageCoordinate = e.originalEvent['clientX'];
        }

        return pageCoordinate - rangePos;
    }
};
export {rsView};